<template>
<div class="table-scroll">
    <table class="table table-fixed-last-column">
        <thead class="table-dark">
            <tr>
                <th class="rounded-start" scope="col">#</th>
                <th>Tên công ty</th>
                <th>Mã số thuế</th>
                <!-- <th>Mã LĐ/TT</th> -->
                <th>Ngày HĐ</th>
                <th>Loại</th>
                <th>HT</th>
                <th scope="col">Đầu mối</th>
                <th class="fixed-column">Tổng tiền (VAT)</th>
                <th class="rounded-end" scope="col"></th>
            </tr>
        </thead>
        <tbody>
            <template v-for="(item, index) in contracts">
                <tr :key="`parent-${item.contract_id}}`">
                    <td scope="row">
                        <button class="btn btn-default btn-xs" @click="showDetail(item)">
                            <i class="fa-regular fa-circle-info"></i>
                        </button>
                    </td>
                    <td>
                        <router-link :title="item.company_name" :to="{
                  name: 'contract-edit',
                  params: { id: item.contract_id },
                }">
                            <span class="text-capitalize">{{ item.company_name }}</span>
                        </router-link>
                        <div v-if="item.contract_note">
                            <i class="fa-regular fa-notes me-1"></i>
                            <i class="text-danger  fw-bold"> {{ item.contract_note }}</i>
                        </div>
                    </td>
                    <td>{{ item.company_taxcode }}</td>
                    <!-- <td>{{ item.contract_code }}</td> -->
                    <td>{{ item.contract_date | moment("DD/MM/YYYY") }}</td>
                    <td>
                        <template v-if="item.contract_type == 1">
                            <span class="badge bg-warning bg-opacity-10 text-black"><i class="fa-solid fa-circle-small"></i> Gia hạn</span>
                        </template>
                        <template v-else>
                            <span class="badge bg-success bg-opacity-10 text-success"><i class="fa-solid fa-circle-small"></i> Mới</span>
                        </template>
                    </td>
                    <td>
                        <span v-if="item.contract_status < 3" title="Chưa hoàn thiện" class="text-danger">
                            <i class="fa-regular fa-circle-xmark"></i>
                        </span>
                        <span v-else class="text-success" :title="'Đã hoàn thiện ' + item.complete_date">
                            <i class="fa-regular fa-circle-check"></i>
                        </span>
                    </td>
                    <td>
                        {{ item.customer_name }}
                    </td>
                    <td class="fixed-column">
                        <strong class="text-danger">{{ formatAmout(item.contract_total) }}đ</strong>
                    </td>
                    <td>
                        <div class="dropdown">
                            <button class="btn btn-sm border-0" type="button" data-bs-toggle="dropdown" aria-expanded="false">
                                <i class="fa-solid fa-ellipsis-vertical"></i>
                            </button>
                            <ul class="dropdown-menu">
                                <li>
                                    <router-link class="dropdown-item hand" :to="{
                        name: 'contract-detail',
                        params: {
                          id: item.contract_id,
                        },
                      }">
                                        <i class="fa-thin fa-circle-info"></i> Xem chi tiết
                                    </router-link>
                                </li>
                                <li>
                                    <router-link class="dropdown-item" :to="{
                        name: 'contract-edit',
                        params: { id: item.contract_id },
                      }">
                                        <i class="fa-thin fa-pen-to-square"></i> Cập nhật hợp đồng
                                    </router-link>
                                </li>
                                <li v-if="item.contract_status < 3">
                                    <a class="dropdown-item hand" @click="completeContract(item, 0)"><i class="fa-thin fa-circle-check"></i> Hoàn thiện hợp đồng</a>
                                </li>
                                <li v-if="item.contract_payment < 3">
                                    <a class="dropdown-item hand" @click="completeContract(item, 1)"><i class="fa-thin fa-circle-dollar"></i> Thanh toán hợp đồng</a>
                                </li>
                                <li>
                                    <router-link class="dropdown-item hand" title="Tùy chỉnh hợp đồng" :to="{
                        name: 'contract-option',
                        params: {
                          id: item.contract_id,
                        },
                      }">
                                        <i class="fa-thin fa-gear"></i> Tùy chỉnh hợp đồng
                                    </router-link>
                                </li>

                                <template v-if="item.templates.length > 0">
                                    <li>
                                        <hr class="dropdown-divider" />
                                    </li>
                                    <li v-for="template in item.templates" :key="template.temp_id">
                                        <a @click="
                          downloads(item.contract_id, template, item.company_taxcode)
                        " :title="'Tải về ' + template.temp_name" class="dropdown-item hand text-primary fw-bold"><i class="fa-thin fa-cloud-arrow-down"></i>
                                            {{ template.temp_name }}</a>
                                    </li>
                                </template>
                                <li>
                                    <hr class="dropdown-divider" />
                                </li>
                                <li>
                                    <a class="dropdown-item hand" @click="cancelContract(item, index)">
                                        <strong class="text-danger"><i class="fa-thin fa-trash-can"></i> Hủy hợp đồng</strong></a>
                                </li>
                            </ul>
                        </div>
                    </td>
                </tr>
            </template>
        </tbody>
    </table>
    <completeContract ref="completeContract" @updateCompleteContract="updateCompleteContract"></completeContract>

    <contractDetail ref="modalContractDetail"> </contractDetail>
</div>
</template>

<script>
import completeContract from "../../extends/ModalCompleteContract.vue";
import contractDetail from "@/extends/ModalContractDetail.vue";
export default {
    components: {
        completeContract,
        contractDetail,
    },
    data() {
        return {};
    },
    props: {
        contracts: Array,
    },
    methods: {
        showDetail(contract) {
            this.$refs.modalContractDetail.show(contract);
        },
        unitPrice(detail) {
            let price = parseFloat(detail.detail_product_price).toFixed(4);
            let quantity = parseInt(detail.detail_quantity);
            return detail.detail_product_price > 0 ?
                this.formatAmout(price * quantity, 4) :
                detail.product.product_name;
        },
        updateCompleteContract(value) {
            let result = this.contracts.filter((item) => {
                return item.contract_id == value.contract_id;
            })[0];
            result.contract_status = value.contract_status;
            result.contract_payment = value.contract_payment;
        },
        completeContract(contract, type) {
            this.$refs.completeContract.show(contract, type);
        },
        cancelContract(item, index) {
            this.$emit("cancelContract", item, index);
        },
        formatAmout(number, gits) {
            return this.$econtract.formatAmout(number, gits);
        },
        getType(type) {
            return type == 0 ? "Gia hạn" : "Mới";
        },
        contractStatus(status) {
            return this.$econtract.contractStatus(status);
        },
        paymentStatus(status) {
            return this.$econtract.paymentStatus(status);
        },
        downloads(contract_id, template, taxcode) {
            try {
                let company_taxcode = taxcode;
                this.$econtract.showLoading();
                let temp_id = template.temp_id;
                axios
                    .post(
                        `${API_URL}/exportcontract`, {
                            contract_id: contract_id,
                            temp_id: temp_id,
                        }, {
                            responseType: "blob",
                        }
                    )
                    .then((response) => {
                        if (company_taxcode == null) company_taxcode = "Nguoi_Mua_Khong_Co_MST";
                        let filename = `${company_taxcode}-${template.temp_display}.docx`;
                        const url = window.URL.createObjectURL(new Blob([response.data]));
                        const link = document.createElement("a");
                        link.href = url;
                        link.setAttribute("download", filename);
                        document.body.appendChild(link);
                        link.click();
                        this.$econtract.hideLoading();
                    });
            } catch (error) {
                this.$toast.danger(error.message);
                this.$econtract.hideLoading();
            }
        },
    },
};
</script>

<style scoped>
.hiddenRow {
    padding: 0 !important;
    border: 0px !important;
}
</style>
