<template>
  <b-modal
    id="modal-contract-detail"
    no-close-on-backdrop
    no-fade
    size="lg"
    ref="modal-add-delivery"
    ignore-enforce-focus-selector="span.select2-container"
  >
    <template #modal-title>{{ title }}</template>
    <table class="table">
      <tbody>
        <tr>
          <td><strong>Tên công ty:</strong></td>
          <td>{{ contract.company_name }}</td>
        </tr>
        <tr>
          <td><strong>Mã số thuế:</strong></td>
          <td>{{ contract.company_taxcode }}</td>
        </tr>
        <tr>
          <td><strong>Số HĐ:</strong></td>
          <td>{{ contract.contract_number }}/{{ contract.contract_number_pattern }}</td>
        </tr>
        <tr>
          <td><strong>Ngày HĐ:</strong></td>
          <td>{{ contract.contract_date | moment("DD/MM/YYYY") }}</td>
        </tr>
        <tr>
          <td><strong>Loại HĐ:</strong></td>
          <td>
            {{ contractType(contract.contract_type) }}
          </td>
        </tr>
        <tr>
          <td><strong>Trạng thái HĐ:</strong></td>
          <td>
            <span :class="'badge text-bg-'+contract_status_class">{{ contractStatus(contract.contract_status) }}</span>
      
          </td>
        </tr>
        <tr>
          <td><strong>Thanh toán:</strong></td>
          <td>
            <span :class="'badge text-bg-'+contract_payment_class">           {{ contractPayment(contract.contract_payment) }}</span>
 
            </td>
        </tr>
        <tr>
          <td><strong>Mã LĐ/TT:</strong></td>
          <td>{{ contract.contract_code }}</td>
        </tr>
      </tbody>
    </table>

    <table class="table table-striped">
      <thead class="table-dark">
        <tr>
          <th class="rounded-start" scope="col">#</th>
          <th>Nội dung</th>
          <th>ĐVT</th>
          <th>SL</th>
          <th>Đơn giá</th>
          <th>TT</th>
          <th class="rounded-end" scope="col">Mã TB</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in contract.details" :key="item.detail_id">
        
          <td>{{ index + 1 }}</td>
          <td>{{ item.detail_content }}</td>
          <td>{{ item.detail_unit }}</td>
          <td>{{ item.detail_quantity }}</td>
          <td>{{ item.detail_product_price | formatamout(0) }}</td>
          <td>
            {{ (item.detail_quantity * item.detail_product_price) | formatamout(0) }}
          </td>
          <td>{{ item.ma_tb }}</td>
        </tr>
      </tbody>
    </table>
    <template #modal-footer>
      <button class="btn btn-secondary" @click="close">
        <i class="fa-thin fa-xmark"></i> Đóng
      </button>
    </template>
  </b-modal>
</template>

<script>
// import contracts from "@/store/modules/contracts";
import moment from "moment";

export default {
  data() {
    return {
      title: "Chi tiết hợp đồng",
      contract: [],
      contract_status_class: 0,
      contract_payment_class: 0,
    };
  },

  methods: {
    show(contract) {
      this.contract = contract;
      this.$bvModal.show("modal-contract-detail");
    },
    close() {
      this.$bvModal.hide("modal-contract-detail");
    },
    contractStatus(status) {
      if (status == 3 || status == 4) this.contract_status_class = "success";
      else this.contract_status_class="warning";

      return this.$econtract.contractStatus(status);
    },
    contractPayment(status) {
      if (status ==3) this.contract_payment_class = "success";
      else this.contract_payment_class="danger";
      return this.$econtract.paymentStatus(status);
    },
    contractType(type) {
      return type == 1 ? "Gia hạn" : "Đăng ký mới";
    },
  },
};
</script>
