<template>
  <div id="contracts">
    <h1 class="fw-bold title-page" title="Danh sách hợp đồng">Danh sách hợp đồng</h1>

    <div class="row">
      <div class="col-12">
        <div class="d-flex justify-content-between">
          <div class="mb-2">
            <div class="input-group">
              <div class="dropdown">
                <button
                  class="btn btn-outline-secondary"
                  type="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                  data-bs-auto-close="outside"
                >
                  <i class="fa-regular fa-gear"></i>
                </button>
                <ul class="dropdown-menu">
                  <li>
                    <div class="dropdown-item">
                      <div class="form-check">
                        <input
                          class="form-check-input"
                          type="checkbox"
                          :value="searchBy.company_name"
                          v-model="searchBy.company_name"
                          id="flexCheckDefault"
                        />
                        <label class="form-check-label" for="flexCheckDefault">
                          Tìm theo tên công ty
                        </label>
                      </div>
                    </div>
                  </li>
                  <li>
                    <div class="dropdown-item">
                      <div class="form-check">
                        <input
                          class="form-check-input"
                          type="checkbox"
                          :value="searchBy.taxcode"
                          v-model="searchBy.taxcode"
                          id="flexCheckDefault"
                        />
                        <label class="form-check-label" for="flexCheckDefault">
                          Tìm theo mã số thuế công ty
                        </label>
                      </div>
                    </div>
                  </li>
                  <li></li>
                </ul>
              </div>
              <div class="position-relative d-inline-flex align-items-center">
                <input
                  type="text"
                  class="form-control"
                  v-model="keyword"
                  :placeholder="searchBy.placeholder"
                  aria-label="Username"
                  aria-describedby="basic-addon1"
                  @change="search()"
                  style="min-width: 400px"
                />
              </div>
              <div class="dropdown ms-1">
                <button
                  class="btn btn-primary-soft"
                  type="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                  data-bs-auto-close="outside"
                >
                  <i class="fa-solid fa-bars-filter"></i> Lọc
                </button>
                <ul class="dropdown-menu p-3" style="width: 500px">
                  <li>
                    <h5 class="fw-bold">Tìm kiếm</h5>
                  </li>
                  <li>
                    <div>
                      <div class="row">
                        <div class="col-6">
                          <div class="form-group">
                            <label for="">Từ ngày</label>
                            <date-picker
                              v-model.lazy="searchBy.startDate"
                              :config="options"
                            ></date-picker>
                          </div>
                        </div>

                        <div class="col-6">
                          <div class="form-group">
                            <label for="">Đến ngày</label>
                            <date-picker
                              v-model.lazy="searchBy.endDate"
                              :config="options"
                            ></date-picker>
                          </div>
                        </div>
                      </div>
                    </div>
                  </li>
                  <li>
                    <hr class="dropdown-divider" />
                  </li>
                  <li>
                    <div class="row">
                      <div class="col-6">
                        <div class="form-group">
                          <label for="">Trạng thái hợp đồng</label>
                          <select class="form-select" v-model="searchBy.contract_status">
                            <option value="-1">Tất cả</option>
                            <option value="0">Mới tạo lập</option>
                            <option value="1">Đã trình ký</option>
                            <option value="2">Đã gửi khách hàng</option>
                            <option value="3">Hoàn thiện</option>
                            <option value="4">Đã làm thanh toán</option>
                          </select>
                        </div>
                      </div>

                      <div class="col-6">
                        <div class="form-group">
                          <label for="">Trạng thái thanh toán</label>
                          <select v-model="searchBy.payment_status" class="form-select">
                            <option value="-1">Tất cả</option>
                            <option value="0">Chưa thanh toán</option>
                            <option value="1">Đã nhận tiền nhưng chưa nộp tiền</option>
                            <option value="2">
                              Đã nộp tiền nhưng chưa làm thanh toán
                            </option>
                            <option value="3">Đã nộp tiền và xuất hóa đơn</option>
                          </select>
                        </div>
                      </div>
                    </div>
                  </li>

                  <li>
                    <div class="row">
                      <div class="col-6">
                        <div class="form-group">
                          <label for="">Mã HĐ/TT</label>
                          <input
                            type="text"
                            v-model="searchBy.contract_code"
                            class="form-control"
                            placeholder="Mã hợp đồng"
                          />
                        </div>
                      </div>
                    </div>
                  </li>
                  <li>
                    <button class="btn btn-primary-soft mt-2" @click="advanceSearch">
                      <i class="fa-solid fa-bars-filter"></i> Lọc
                    </button>

                    <button class="btn btn-secondary mt-2 ms-1" @click="search">
                      <i class="fa-regular fa-rotate"></i> Reset
                    </button>
                  </li>
                </ul>
              </div>
            </div>
          </div>

          <div class="">
            <router-link :to="{ name: 'contract-create' }" class="btn btn-primary ms-2">
              <i class="fa-duotone fa-circle-plus"></i> Tạo mới
            </router-link>
          </div>
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-12">
        <tableContracts :contracts="contracts" @cancelContract="cancelContract" />
        <notFound v-if="contracts.length == 0" />
        <pagination
          v-bind:pagination="pagination"
          v-on:click.native="fetchAll({ page: pagination.current_page, name: keyword })"
          :offset="4"
        ></pagination>
      </div>
    </div>
  </div>
</template>

<script>
import pagination from "../../extends/Paginate";
import tableContracts from "../../components/contracts/Index";
import notFound from "../../extends/NotFound.vue";
import axios from "axios";
import datePicker from "vue-bootstrap-datetimepicker";
import "pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css";
import moment from "moment";
import { mapActions, mapState, mapGetters } from "vuex";

export default {
  components: {
    pagination,
    tableContracts,
    notFound,
    datePicker,
  },
  data() {
    return {
      contractResponse: [],
      keyword: "",
      // contracts: [],
      flagSearch: false,
      searchBy: {
        taxcode: false,
        company_name: false,
        placeholder: "Tìm theo tên công ty",
        startDate: moment().startOf("month").format("DD/MM/YYYY"),
        endDate: moment().add(0, "months").endOf("month").format("DD/MM/YYYY"),
        payment_status: 0,
        contract_status: -1,
        page: 1,
        contract_code: "",
      },
      // pagination: {
      //     total: 0,
      //     per_page: 2,
      //     from: 1,
      //     to: 0,
      //     current_page: 1,
      // },
      offset: 4,
      dataLogin: [],
      options: {
        format: "DD/MM/YYYY",
        useCurrent: false,
      },
      debounceTimeout: null,
    };
  },
  watch: {
    "searchBy.company_name": {
      handler(newValue) {
        if (newValue) this.searchBy.placeholder = "Tìm theo tên công ty";
      },
      immediate: true,
    },
    "searchBy.taxcode": {
      handler(newValue) {
        if (newValue) this.searchBy.placeholder = "Tìm theo mã số thuế công ty";
      },
      immediate: true,
    },
  },
  computed: {
    ...mapGetters("contracts", ["contracts", "pagination"]),
  },

  created() {
    if (this.$route.query.query) {
      this.keyword = this.$route.query.query;
      this.fetchAll({
        page: 1,
        name: this.keyword,
      });
    } else {
      this.fetchAll(this.searchBy);
    }

    //this.fetchAll(this.searchBy);
  },
  methods: {
    ...mapActions("contracts", ["getContracts"]),
    // debouncedSearch() {
    //   clearTimeout(this.debounceTimeout); // Xóa timeout cũ khi người dùng tiếp tục nhập liệu
    //   this.debounceTimeout = setTimeout(() => {
    //     this.search();
    //   }, 500); // Gọi search sau 500ms kể từ lần nhập cuối cùng
    // },
    advanceSearch() {
      this.fetchAll({
        date: {
          startDate: this.searchBy.startDate,
          endDate: this.searchBy.endDate,
        },
        page: this.pagination.current_page,
        payment: this.searchBy.payment_status,
        status: this.searchBy.contract_status,
        code: this.searchBy.contract_code,
      });
    },
    search() {
      const newQuery = this.keyword ? { query: this.keyword } : null;
      if (this.$route.query.query !== this.keyword) {
        this.$router.push({ query: newQuery }).catch((err) => {
          if (err.name !== "NavigationDuplicated") {
            throw err;
          }
        });
      }
      this.fetchAll({
        page: this.pagination.current_page,
        name: this.keyword,
      });
    },
    cancelContract(item, index) {
      this.$confirm({
        title: "Hủy hợp động",
        message: "Bạn có muốn hủy hợp đồng?",
        button: {
          yes: "Yes",
          no: "No",
        },
        callback: (confirm) => {
          if (confirm) {
            axios.delete(`/contracts/${item.contract_id}`).then((response) => {
              if (response.status == 200) {
                this.contracts.splice(index, 1);
                this.$toast.success("Hủy hợp đồng thành công");
              }
            });
          }
        },
      });
    },
    formattedDate(date) {
      return date;
    },
    async fetchAll(searchArray) {
      this.$econtract.showLoading();
      clearTimeout(this.debounceTimeout); // Xóa timeout cũ khi người dùng tiếp tục nhập liệu
      this.debounceTimeout = setTimeout(() => {
        // if (
        //   typeof searchArray.name === "undefined" ||
        //   (searchArray.name === "" && searchArray.page > 1)
        // ) {
        //   searchArray.page = 1;
        // }
        // searchArray.page = this.pagination.current_page;
        this.getContracts(searchArray).then(() => {
          this.$econtract.hideLoading();
        });
      }, 300);
    },
  },
};
</script>
